<template>
  <div class="content">
    <input type="button" @click="showToast(1)" value=""/>
    <input type="button" @click="showToast(2)" value=""/>
    <input type="button" @click="showToast(3)" value=""/>
    <input type="button" @click="showToast(4)" value=""/>
    <input type="button" @click="showToast(5)" value=""/>
    <input type="button" @click="showToast(6)" value=""/>
    <input type="button" @click="showToast(7)" value="1s"/>
    <Toast @toast-hide="toastHide" :show="toast.show" :position="toast.position" :type="toast.type" :time="toast.time"
            :msg="toast.msg"></Toast>
  </div>
</template>

<script>
import Toast from 'tinny-vue-ui/src/Toast.vue'
export default {
  components: {
    Toast
  },
  data () {
    return {
      toast: { //  
        show: true,
        position: 'top',
        type: 'text',
        time: '2000',
        msg: 'hello vueUI'
      }
    }
  },
  methods: {
    toastHide () { // , vue2.0
      this.toast.show = false
    },
    showToast (item) {
      switch (item) {
        case 1:
          this.toast = {
            show: true,
            position: 'middle',
            type: 'text',
            time: '3000',
            msg: ''
          }
          break
        case 2:
          this.toast = {
            show: true,
            position: 'middle',
            type: 'text',
            time: '2000',
            msg: '2s'
          }
          break
        case 3:
          this.toast = {
            show: true,
            position: 'top',
            type: 'text',
            time: '3000',
            msg: ''
          }
          break
        case 4:
          this.toast = {
            show: true,
            position: 'bottom',
            type: 'text',
            time: '3000',
            msg: ''
          }
          break
        case 5:
          this.toast = {
            show: true,
            position: 'middle',
            type: 'warn',
            time: '3000',
            msg: ''
          }
          break
        case 6:
          this.toast = {
            show: true,
            position: 'middle',
            type: 'cancel',
            time: '6000',
            msg: ', 6s'
          }
          break
        case 7:
          this.toast = {
            show: true,
            position: 'middle',
            type: 'cancel',
            time: '1000',
            msg: '1s, '
          }
          break
      }
    }
  }
}
</script>

<style scoped>

</style>



