<template>
  <div class="content">
    <input-item
      class="mgb10"
      :valid-callback="validCallback"
      :value="title" :pattern="titlePattern"
      :maxlength="15"
      :clip=true
      :visible-error="true"
      maxlength-errormsg="15"
      required-errormsg=""
      name='title'
      placeholder-text="15"></input-item>
    <input-item
      class="mgb10"
      :valid-callback="validCallback"
      :value="title" :pattern="titlePattern"
      :maxlength="15"
      :clip=false
      :visible-error="true"
      maxlength-errormsg="15"
      required-errormsg=""
      name='title'
      placeholder-text="15"></input-item>
    <input-item
      class="mgb10"
      :valid-callback="validCallback"
      :value="title" :pattern="titlePattern"
      :clip=true
      :visible-error="true"
      maxlength-errormsg="15"
      required-errormsg=""
      name='title'
      placeholder-text=""></input-item>
    <input-item
      class="mgb10"
      :valid-callback="validCallback"
      :value="title" :pattern="titlePattern"
      :clip=true
      :visible-error="false"
      maxlength-errormsg="15"
      required-errormsg=""
      name='title'
      placeholder-text=""></input-item>
    <input-item
      class="mgb10"
      :disable="true"
      name='title'
      placeholder-text=""></input-item>
    <input-item
      tag="textarea"
      :valid-callback="validCallback"
      :value.sync="title" :pattern="titlePattern"
      :maxlength="100"
      :clip=false
      :visible-error="true"
      maxlength-errormsg="100"
      required-errormsg=""
      name='title'
      placeholder-text=""></input-item>
    </div>
</template>

<script>
import InputItem from 'tinny-vue-ui/src/Inputs.vue'
export default {
  name: 'inputs',
  data () {
    return {
      title: '',
      titlePattern: [
        {
          name: '',
          reg: /^1/,
          errormsg: '1'
        },
        {
          name: '',
          reg: /2$/,
          errormsg: '2'
        }
      ]
    }
  },
  components: {
    InputItem
  },
  methods: {
    validCallback () {
      console.log('arguments', arguments)
    }
  }
}
</script>

<style scoped>
  @import '../static/common.scss';
  .content {
    padding: 20px;
  }
</style>



