<template>
  <div class="content">
    <div class="common">
      <p></p>
      <folder>
        <folder-item v-for="(item, idx) in common" :key="idx" :title="item.title">
          <div slot="content">
            <p v-for="(para, index) in item.content" v-bind:key="index">{{para}}</p>
          </div>
        </folder-item>
      </folder>
    </div>
    <div class="accordion">
      <p></p>
      <folder accordion="true" @folder-accordion="folderAccordion">
        <folder-item v-for="(item, idx) in accordion" :key="idx"
                     :unfolder="idx===selIdx?unfolder:!unfolder"
                     :title="item.title">
          <div slot="content">
            <p v-for="(para, index) in item.content" v-bind:key="index">{{para}}</p>
          </div>
        </folder-item>
      </folder>
    </div>
  </div>
</template>

<script>
import Folder from 'tinny-vue-ui/src/Folder.vue'
import FolderItem from 'tinny-vue-ui/src/FolderItem.vue'
export default {
  components: {
    Folder,
    FolderItem
  },
  methods: {
    folderAccordion: function (now) {
      this.selIdx = now
    }
  },
  data () {
    return {
      selIdx: -1,
      unfolder: true,
      common: [
        {
          title: '',
          content: [
            '',
            ''
          ]
        },
        {
          title: '',
          content: [
            ''
          ]
        },
        {
          title: '',
          content: [
            '102030',
            '100',
            ''
          ]
        },
        {
          title: '',
          content: [
            '',
            '',
            '',
            ''
          ]
        },
        {
          title: '',
          content: [
            '',
            '',
            ''
          ]
        }
      ],
      accordion: [
        {
          title: '',
          content: ['',
            '?',
            ';'
          ]
        },
        {
          title: '',
          content: [
            '',
            '810',
            '1!',
            '1'
          ]
        },
        {
          title: '',
          content: [
            '......',
            '',
            ''
          ]
        },
        {
          title: ' ',
          content: [
            'A!!',
            'AB',
            'C'
          ]
        },
        {
          title: '',
          content: [
            '?',
            '??',
            '',
            '! '
          ]
        }

      ]
    }
  }
}
</script>

<style scoped>
@import "../static/common.scss";
  .content {
    padding: 20px;
  }
  .inner-content p {
    text-indent: 2rem;
  }
</style>



